.loader__ellips {
  font-size: 20px; /* change size here */
  position: relative;
  width: 4em;
  height: 1em;
  margin: 10px auto;
}

.loader__ellips__dot {
  display: block;
  width: 1em;
  height: 1em;
  border-radius: 0.5em;
  background: #555; /* change color here */
  position: absolute;
  animation-duration: 0.5s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}

.loader__ellips__dot:nth-child(1),
.loader__ellips__dot:nth-child(2) {
  left: 0;
}
.loader__ellips__dot:nth-child(3) {
  left: 1.5em;
}
.loader__ellips__dot:nth-child(4) {
  left: 3em;
}

@keyframes reveal {
  from {
    transform: scale(0.001);
  }
  to {
    transform: scale(1);
  }
}

@keyframes slide {
  to {
    transform: translateX(1.5em);
  }
}

.loader__ellips__dot:nth-child(1) {
  animation-name: reveal;
}

.loader__ellips__dot:nth-child(2),
.loader__ellips__dot:nth-child(3) {
  animation-name: slide;
}

.loader__ellips__dot:nth-child(4) {
  animation-name: reveal;
  animation-direction: reverse;
}
