.Lightbox__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.Lightbox__content {
  display: flex;
  justify-content: center;
  align-items: center;

  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 96px);
  /* max-width: calc(100vw - 600px);
  max-height: calc(100vh - 400px); */
}
